input[type=checkbox] {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    visibility: hidden;
}

.custom-toggle{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    position: absolute;
    width: 20px;
    width: var(--checkbox-size);
    height: 20px;
    height: var(--checkbox-size);
    border-radius: 4px;
    background-color: transparent;
    border: 2px solid var(--text-color);
}

/* hover */
.label-container:hover input ~ .custom-toggle {
    border: 2px solid var(--color-hover);
}

/* click */
.label-container input:active ~ .custom-toggle {
    -webkit-animation: border-click 0.1s;
            animation: border-click 0.1s;
    
}
  
/* checked */
.label-container input:checked ~ .custom-toggle {
    /* background-color: green; */
}

.custom-toggle:after {
    content: "";
    position: absolute;
    display: none;
}

.label-container input:checked ~ .custom-toggle:after {
    display: block;
}
  
/* checkmark */
.label-container .custom-toggle:after {
    width: 80%;
    height: 80%;
    border-radius: 2px;
    -webkit-animation: checkmark-grow 0.2s;
            animation: checkmark-grow 0.2s;
    background-color: var(--text-color);
}

@-webkit-keyframes checkmark-grow{
    0%{
        width: 0px;
        height: 0px
    }
    50%{
        width: 0px;
        height: 0px;
    }
    80%{
        width: 100%;
        height: 100%;
    }
    100%{
        width: 80%;
        height: 80%;
    }
}

@keyframes checkmark-grow{
    0%{
        width: 0px;
        height: 0px
    }
    50%{
        width: 0px;
        height: 0px;
    }
    80%{
        width: 100%;
        height: 100%;
    }
    100%{
        width: 80%;
        height: 80%;
    }
}

@-webkit-keyframes border-click{
    0%{
        width: 20px;
        width: var(--checkbox-size);
        height: 20px;
        height: var(--checkbox-size);
    }
    100%{
        margin-left: 1px;
        margin-top: 1px;
        width: calc(20px - 2px);
        width: calc(var(--checkbox-size) - 2px);
        height: calc(20px - 2px);
        height: calc(var(--checkbox-size) - 2px);
    }
}

@keyframes border-click{
    0%{
        width: 20px;
        width: var(--checkbox-size);
        height: 20px;
